* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
  
}

.slide {
  opacity: 0;
  transition-duration: 0.6s ease;
  max-width: 95vw;
}

.slide.active {
  opacity: 1;
  transition-duration: 0.6s;
  transform: scale(1.08);
  max-width: 95vw;
}

.slider-button{
  box-shadow: rgba(61,165,120, .1) 0 0 0 1px inset, rgba(61,165,120, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(61,165,120, .3) 0 0 0 4px;
}


.fade-in-section {
  opacity: 0;
  transform: translateY(10vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

.leftarrow{
  position:absolute;
  z-index: 1000;
  left:0%;
  top: 45%;
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, .1);
  padding: 8px;
}

.rightarrow {
  position:absolute;
  z-index: 1000;
  right:0%;
  top: 45%;
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, .1);
  padding: 8px;

}